<template>
  <div id="app">
  <v-app id="inspire">
    <v-navigation-drawer
      fixed
      :clipped="$vuetify.breakpoint.mdAndUp"
      app
      v-model="drawer">
      <v-list dense>
        <template v-for="item in items">
   
        <router-link :to="item.routerLink"  :key="item.text">
          <v-list-tile @click="" v-ripple>
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>
                {{ item.text }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </router-link>

        </template>
      </v-list>
    </v-navigation-drawer>

    
    <v-toolbar
      color="blue darken-3"
      dark
      app
      :clipped-left="$vuetify.breakpoint.mdAndUp"
      fixed>
      <v-toolbar-title style="width: 300px; text-align: left;" class="ml-0 pl-3">
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <span class="">Material Page</span>
      </v-toolbar-title>
      
      <div class="text-xs-center">
        <v-menu open-on-hover right offset-y>
        <v-btn icon large  slot="activator" class="hidden-sm-and-down">
          <v-avatar size="32px" tile>
              <img
                :src="getSearchIcon"
                alt="search">
          </v-avatar>
        </v-btn>
        <v-list>
          <v-list-tile v-for="(item, index) in searcher" :key="index" @click="selectSearch(index)">
            {{ item.text }}
          </v-list-tile>
        </v-list>
        </v-menu>
      </div>
  
      <v-text-field
        flat
        solo-inverted
        prepend-icon="search"
        label="Search"
        class="hidden-sm-and-down"
        v-model="searchInput"
        v-on:keyup.13="doSearch()"
      ></v-text-field>
      
      <v-spacer></v-spacer>
      <v-tooltip bottom>
        <v-btn icon large  slot="activator"  target="_blank" href="https://github.com/zc95/material-page">
        <v-avatar size="32px" tile>
            <img
              src="data:image/svg+xml;base64,"
              alt="Vuetify"
            >
        </v-avatar>
      </v-btn>
        <span>Give me <br> a star</span>
      </v-tooltip>
    </v-toolbar>

    
    <v-content style="padding-top: 0;">
      <v-container>
       <keep-alive>
            <router-view></router-view>
        </keep-alive>
      </v-container>
    </v-content>

  </v-app>
</div>
</template>

<script type="text/ecmascript-6">

export default {
  props: {
    source: String
  },
  data() {
    return {
      dialog: false,
      drawer: null,
      items: [
        { icon: "home", text: "HomePage", routerLink:'/homepage', color:'red' },
        { icon: "link", text: "Links", routerLink:'/links', color:'orange'  },
        { icon: "list", text: "Todo", routerLink:'/todos', color:'green'  },
        { icon: "settings", text: "Settings", routerLink:'/settings', color:'yellow'  }
      ],
      searcher: [
        {
          icon:
            "data:image/svg+xml;base64,",
          text: "Bing"
        },
        {
          icon:
            "data:image/svg+xml;base64,",
          text: "Baidu"
        },
        {
          icon:
            "data:image/svg+xml;base64,",
          text: "Google"
        }
      ],
      getSearchIcon:
        localStorage.searchIcon ||
        "data:image/svg+xml;base64,",
      searchInput: ""
    };
  },
  methods: {
    selectSearch(index) {
      //选择搜索引擎
      localStorage.searchType = this.searcher[index].text;
      localStorage.searchIcon = this.searcher[index].icon;
      this.getSearchIcon = this.searcher[index].icon;
    },
    doSearch() {
      //搜索
      let searchType = localStorage.searchType || "Bing";
      if (this.searchInput.trim() === "") {
        return false;
      } else if (searchType == "Baidu") {
        window.open("https://www.baidu.com/s?wd=" + this.searchInput);
      } else if (searchType == "Bing") {
        window.open("https://cn.bing.com/search?q=" + this.searchInput);
      } else if (searchType == "Google") {
        window.open("https://www.google.com.hk/search?q=" + this.searchInput);
      }
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.list--dense .list__tile:not(.list__tile--avatar) {
  height: 55px;
  font-size: 14px;
}
.list--dense a{
  text-decoration: none;
  color: #212121 !important;
  width: 100%;
    padding: 0;
    text-indent: 20px;
}

.navigation-drawer .list--dense .router-link-exact-active .material-icons,.navigation-drawer .list--dense .router-link-exact-active .list__tile{
  color: #09788b !important;
}

</style>



